:root {
    /* Backgrounds - tom azul/cinza moderno */
    --background: 220 25% 97%;
    --foreground: 220 20% 15%;

    /* Cards - branco com leve tom azul */
    --card: 220 30% 99%;
    --card-foreground: 220 20% 15%;

    /* Popovers */
    --popover: 220 30% 99%;
    --popover-foreground: 220 20% 15%;

    /* Teal Primary - MUITO mais vibrante (quase ciano) */
    --primary: 174 80% 40%;
    --primary-foreground: 0 0% 100%;

    /* Secondary - azul acinzentado */
    --secondary: 220 20% 94%;
    --secondary-foreground: 220 20% 25%;

    /* Muted */
    --muted: 220 20% 94%;
    --muted-foreground: 220 15% 48%;

    /* Accent - teal mais claro */
    --accent: 174 70% 90%;
    --accent-foreground: 174 80% 30%;

    /* Destructive */
    --destructive: 0 75% 55%;
    --destructive-foreground: 0 0% 100%;

    /* Borders - mais visíveis */
    --border: 220 20% 88%;
    --input: 220 20% 85%;
    --ring: 174 80% 40%;

    /* Radius - MUITO mais arredondado */
    --radius: 1.25rem;

    /* Sidebar - dark mode refinado */
    --sidebar-background: 220 20% 11%;
    --sidebar-foreground: 220 15% 95%;
    --sidebar-primary: 174 72% 45%;
    --sidebar-primary-foreground: 0 0% 100%;
    --sidebar-accent: 220 18% 16%;
    --sidebar-accent-foreground: 220 15% 95%;
    --sidebar-border: 220 18% 15%;
    --sidebar-ring: 220 15% 80%;
  }

*{
  border-color: hsl(var(--border));
}

body{
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
    font-family: 'Plus Jakarta Sans', 'Inter', ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, sans-serif !important;
    font-weight: 400;
    font-feature-settings: 'rlig' 1, 'calt' 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

